import request from '@/utils/request'
import { ListQuery } from '@/domains/listQuery'

interface IUserId {
  qqUserId: string
}

export function fetchList(query: ListQuery) {
  return request({
    url: '/social/sysqquser',
    method: 'get',
    params: query
  })
}

export function deleteData(data: string[]) {
  return request({
    url: '/social/sysqquser',
    method: 'delete',
    data
  })
}

export function fetchUserId(query: IUserId) {
  return request({
    url: '/social/getSysUserIdThruQqUserId',
    method: 'get',
    params: query
  })
}

export function unBindUser(query: IUserId) {
  return request({
    url: '/social/sysqquser/unbind',
    method: 'delete',
    params: query
  })
}
